import { Redirect, Route, Switch } from 'react-router-dom';
import MainNavigation from './components/layout/MainNavigation';
import QuoteList from './components/quotes/QuoteList';

function App() {
  return (
    <div>
      <MainNavigation />
      <Switch>
        <Route path="/" exact>
          <Redirect to="/QuoteList" />
        </Route>
        <Route path="/quoteList">
          <QuoteList />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
